<template>
  <b-card no-body class="h-100">
    <b-card-body class="p-2">
      <iframe v-if="location.formatted !== ''"
        width="100%"
        height="100%"
        style="border:0"
        :src="'https://www.google.com/maps/embed/v1/place?key=' + googleAPIKey + '&q=' + location.formatted"
        allowfullscreen
        @load="onLoad"
      ></iframe>
    </b-card-body>
  </b-card>
</template>

<script>
import { BCard, BCardBody } from "bootstrap-vue";
export default {
  name: "Map",
  components: {
    BCard,
    BCardBody
  },
  data: function() {
    return {
      googleAPIKey: process.env.VUE_APP_GOOGLE_EMBED_KEY
    }
  },
  props: {
    location: {
      type: Object,
      required: true
    }
  },
  methods: {
    onLoad(){
      this.$emit("ready");
    }
  }
};
</script>